  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
  border: 1px solid transparent;
  padding: 5px; }
  .widgets-drop-container > div {
    background: #3399ff;
    -ms-flex: 1 1;
        flex: 1 1;
    min-height: 200px;
